import { Component, OnInit } from '@angular/core';
import { Router,ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';
@Component({
  selector: 'app-module-person-course',
  templateUrl: './module-person-course.component.html',
  styleUrls: ['./module-person-course.component.css']
})
export class ModulePersonCourseComponent implements OnInit {
  public items;
  public course;
  public incourse;
  constructor(
    private route: ActivatedRoute,private router: Router,private http: HttpClient
  ) { }

  ngOnInit(): void {
    this.getItems()
    this.getCourses()
  }
  getItems(){
    const firstParam: string = this.route.snapshot.queryParamMap.get('rut');
    if(firstParam){
      this.http.post<any>('http://127.0.0.1:5000/detallePerson',{rut:firstParam}).subscribe(data => {
        this.items = data[0];
      });
    }else{
      this.items = {rut:null, name:'', lastname:'', birth:'', address:''}
    }
    
  }
  setcourse(course){
    const firstParam: string = this.route.snapshot.queryParamMap.get('rut');
    this.http.post<any>('http://127.0.0.1:5000/inserthistory',{idcourse:course.ID, idperson:firstParam}).subscribe(data => {
      this.getCourses()
    });
  }
  getcoursesall(){
    this.http.get<any>('http://127.0.0.1:5000/detallecourseall').subscribe(data => {
      this.incourse = data;
    });
  }
  getCourses(){
    const firstParam: string = this.route.snapshot.queryParamMap.get('rut');
    this.http.post<any>('http://127.0.0.1:5000/detallePersoncourse',{rut:firstParam}).subscribe(data => {
      this.course = data;
    });
  }
  changeItem(){
    const firstParam: string = this.route.snapshot.queryParamMap.get('rut');
    if(firstParam){
      this.http.post<any>('http://127.0.0.1:5000/updatePerson',this.items).subscribe(data => {
        this.getItems()
      });
    }else{
      this.http.post<any>('http://127.0.0.1:5000/insertPerson',this.items).subscribe(data => {
        console.log(data)
        if(data){
          this.router.navigate(['personModule/courses'], { queryParams: { rut: this.items.rut} });
        }
        
      });
    }
    
  }
}
